.section-guarantee__title{
    text-align: center;
    font-size: 26px;
    margin-bottom: 40px;
}
.guarantee-list{
    position: relative;
    width: 100%;
    padding-top: 40px;
    padding-bottom: 40px;
    padding-left: 7px;
    background-color: #F1F1F1;
}
.guarantee-list__item:not(:last-child){
    padding-bottom: 24px;
    border-left: 1px dashed var(--primary-text-color);
}

.guarantee-list__item::before{
    display: block;
    position: absolute;
content: '';
background-image: url(/images/campfire.svg);
width: 15px;
height: 20px;
left: 1px;
}
.guarantee-list__text{
    margin-left: 28px;
    color: var(--fourth-text-color);
    font-size: 12px;
    line-height: 1.67;
    letter-spacing: 0;
}
.guarantee-list__accent{
color: var(--accent-color);
}
@media screen and (min-width:$tablet){

    .guarantee-list {
        padding-top: 32px;
        padding-right: 32px;
        padding-left: 94px;
    }
    .guarantee-list__item:not(:last-child) {
            padding-bottom: 20px;
    }
    .guarantee-list__item::before {
        width: 15px;
        height: 20px;
        background-repeat: no-repeat;
        background-size: cover;
        left: 87px;
    }
        .guarantee-list__text {
            margin-left: 32px;
        }
}
@media screen and (min-width:$desktop) {
.section-guarantee__title {
        font-size: 46px;
    }

    .guarantee-list {
        padding: 60px 280px;  
        }
    .guarantee-list__item{
    }
    .guarantee-list__item::before {
        width: 22px;
        height: 30px;
        left: 270px;
    }
    .guarantee-list__text{
        margin-left: 30px;
    font-size: 20px;
        line-height: 1.4;
    }
}